.dataTables_wrapper .dataTables_length label {
  display: flex;
  align-items: center;
  column-gap: 4px;
}
.dataTables_wrapper .dataTable thead tr th {
  background-color: #222;
  color: #fff;
  transition: all 0.5s;
}
