  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  padding: 8px 18px;
  line-height: 1;
  font-weight: 600;
  border: none;
  transition: all 0.5s;
}
.dataTables_wrapper .dataTable tbody .acciones .accion:hover {
  background-color: #00d6b0;
  color: #2d2d2d;
}

